import React, { useState } from "react";
import { Row, Col, Nav, Tab, Accordion } from "react-bootstrap";

const StockTradingFeatures = () => {
  const [activeKey, setActiveKey] = useState(null);

  const toggleAccordion = (eventKey) => {
    setActiveKey(eventKey === activeKey ? null : eventKey);
  };

  return (
    <Tab.Container id="left-tabs-example" defaultActiveKey="first">
      <Row
        style={{
          rowGap: "20px",
        }}
      >
        <Col sm={12}>
          <Nav variant="pills" className="stock_feature_tabs mt-5">
            <Nav.Item>
              <Nav.Link eventKey="first">User Features</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="second">Admin Features</Nav.Link>
            </Nav.Item>
          </Nav>
        </Col>
        <Col sm={12}>
          <Tab.Content>
            <Tab.Pane eventKey="first">
              <div className="stock_trading_accordion">
                <Accordion activeKey={activeKey} defaultActiveKey="0" flush>
                  <Accordion.Item
                    eventKey="0"
                    className={`accordion-item ${
                      activeKey === "0" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("0")}>
                      User Profile Dashboard
                    </Accordion.Header>
                    <Accordion.Body>
                      A customizable dashboard for users to manage their
                      profiles and monitor trading activities on the go.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="1"
                    className={`accordion-item ${
                      activeKey === "1" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("1")}>
                      Newsfeed
                    </Accordion.Header>
                    <Accordion.Body>
                      Timely information is crucial when it comes to stock
                      trading. For this purpose, we include a curated news feed
                      for accurate updates and insights.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="2"
                    className={`accordion-item ${
                      activeKey === "2" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("2")}>
                      Secure Login (Authentication)
                    </Accordion.Header>
                    <Accordion.Body>
                      Just like any other mobile banking app, users need to
                      create accounts and confirm their identities to comply
                      with KYC regulations. Two-factor authentication adds an
                      extra layer of security.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="3"
                    className={`accordion-item ${
                      activeKey === "3" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("3")}>
                      Analytics
                    </Accordion.Header>
                    <Accordion.Body>
                      This provides trading charts, indicators, and analytical
                      data to help users make informed decisions.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="4"
                    className={`accordion-item ${
                      activeKey === "4" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("4")}>
                      Custom Search (Filters)
                    </Accordion.Header>
                    <Accordion.Body>
                      This feature makes it easy for users to find specific
                      stocks and assets quickly.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="5"
                    className={`accordion-item ${
                      activeKey === "5" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("5")}>
                      Favorites
                    </Accordion.Header>
                    <Accordion.Body>
                      This allows users to save their preferred stocks and
                      assets for easy access.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="6"
                    className={`accordion-item ${
                      activeKey === "6" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("6")}>
                      Push Notifications
                    </Accordion.Header>
                    <Accordion.Body>
                      Notifications keep users informed with real-time market
                      updates and asset volatility alerts.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="7"
                    className={`accordion-item ${
                      activeKey === "7" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("7")}>
                      Limit Orders
                    </Accordion.Header>
                    <Accordion.Body>
                      Supports automated trading strategies with limit order
                      functionalities like "Take Profit" and "Stop Limit" to
                      manage risk.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="8"
                    className={`accordion-item ${
                      activeKey === "8" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("8")}>
                      User-Friendly Interface
                    </Accordion.Header>
                    <Accordion.Body>
                      Our stock trading platform has been seamlessly designed
                      for beginners and experts alike.
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              </div>
            </Tab.Pane>
            <Tab.Pane eventKey="second">
              <div className="stock_trading_accordion">
                <Accordion activeKey={activeKey} defaultActiveKey="0" flush>
                  <Accordion.Item
                    eventKey="0"
                    className={`accordion-item ${
                      activeKey === "0" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("0")}>
                      User Management
                    </Accordion.Header>
                    <Accordion.Body>
                      Admin oversees user accounts, ensuring secure login and
                      managing profiles on the stock trading platform.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="1"
                    className={`accordion-item ${
                      activeKey === "1" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("1")}>
                      Security Features
                    </Accordion.Header>
                    <Accordion.Body>
                      Two-Factor Authentication (2FA) enhances stock app
                      security, with admin auditing and monitoring for user
                      safety.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="2"
                    className={`accordion-item ${
                      activeKey === "2" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("2")}>
                      Stock Listings and Management
                    </Accordion.Header>
                    <Accordion.Body>
                      Admins control stock symbols, integrating real-time market
                      data for accurate stock information on the trading
                      platform.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="3"
                    className={`accordion-item ${
                      activeKey === "3" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("3")}>
                      Risk Management
                    </Accordion.Header>
                    <Accordion.Body>
                      Admins use risk tools, setting position limits to
                      safeguard against trading risks and ensure a secure
                      platform.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="4"
                    className={`accordion-item ${
                      activeKey === "4" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("4")}>
                      Communication and Support
                    </Accordion.Header>
                    <Accordion.Body>
                      Messaging system and customer support dashboard facilitate
                      efficient communication and issue resolution between
                      admins and stock traders.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="5"
                    className={`accordion-item ${
                      activeKey === "5" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("5")}>
                      Analytics and Reporting
                    </Accordion.Header>
                    <Accordion.Body>
                      Admins monitor platform performance, generate user
                      activity reports, and analyze financial data for informed
                      decision-making.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="6"
                    className={`accordion-item ${
                      activeKey === "6" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("6")}>
                      Notification and Alerts
                    </Accordion.Header>
                    <Accordion.Body>
                      Real-time alerts notify users and admins of critical
                      events, ensuring timely response and proactive issue
                      resolution.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item
                    eventKey="7"
                    className={`accordion-item ${
                      activeKey === "7" ? "accordion_item_active" : ""
                    }`}
                  >
                    <Accordion.Header onClick={() => toggleAccordion("7")}>
                      Platform Monitoring
                    </Accordion.Header>
                    <Accordion.Body>
                      Admins employ real-time dashboards, server monitoring, and
                      network oversight for optimal stock trading platform
                      performance.
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              </div>
            </Tab.Pane>
          </Tab.Content>
        </Col>
      </Row>
    </Tab.Container>
  );
};

export default StockTradingFeatures;
