import React from "react";
import { Col, Container, Row } from "react-bootstrap";

const AdvancedFeatures = () => {
  return (
    <Container>
      <Row>
        <Col lg={12}>
          <div className="stock_advance_features_sec">
            <div className="advance_title">
              <h2 className="text-center">
                Advanced Features of Our Stock Trading Application
              </h2>
            </div>
            <Row className="stock_ad_fea_row">
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">Trading with Algos</div>
                  <div className="stock_ad_fea_body">
                    Enhance stock trading with algorithmic strategies for
                    optimal returns. Algorithmic trading strategies and tools
                    for efficient and automated stock market transactions.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">Trading Automation</div>
                  <div className="stock_ad_fea_body">
                    Achieve trading efficiency through automated processes.
                    Implement trading automation for streamlined execution,
                    reducing manual efforts and enhancing accuracy.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">Real-Time Alerts</div>
                  <div className="stock_ad_fea_body">
                    Receive instant notifications for critical market events.
                    Real-time alerts for stock market updates, enabling prompt
                    decision-making and proactive response.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">
                    Integrations with Third Parties
                  </div>
                  <div className="stock_ad_fea_body">
                    Boost functionality with seamless third-party connections.
                    Integrations with external services for enhanced features
                    and a comprehensive stock trading experience.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">Advanced Analytics</div>
                  <div className="stock_ad_fea_body">
                    Unlock insights with advanced analytical tools. Utilize
                    cutting-edge analytics for in-depth market analysis and
                    informed decision-making in stock trading.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">
                    Investment Simulation
                  </div>
                  <div className="stock_ad_fea_body">
                    Practice trading strategies with virtual portfolios.
                    Investment simulation platforms for risk-free practice and
                    testing of trading strategies before real-market
                    implementation.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">Risk Management</div>
                  <div className="stock_ad_fea_body">
                    Safeguard investments with robust risk strategies. Implement
                    risk management tools for effective mitigation and
                    protection against potential losses in stock trading.
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <div className="stock_ad_fea_card">
                  <div className="stock_ad_fea_header">Portfolio Analysis</div>
                  <div className="stock_ad_fea_body">
                    Optimize portfolio performance with detailed analysis.
                    Comprehensive portfolio analysis tools for assessing and
                    refining investment portfolios in the stock market.
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default AdvancedFeatures;
